<template>
  <div>
    <el-col :span="8">
    <el-form ref="MemberDTO" v-model="MemberDTO" label-width="120px">
        <el-form-item label="">
            <el-col :span="12" style="margin-right:20px;">
                <el-input v-model="MemberDTO.mbId"></el-input>
            </el-col>
            <el-col :span="4">
                <el-button @click="idCk"></el-button>
            </el-col>
            <el-col>
            <span>{{ this.idCkmsg }}</span>
        </el-col>
        </el-form-item>
        
        <el-form-item label="">
            <el-input type="password" v-model="MemberDTO.pw"></el-input>
        </el-form-item>
        
        <el-form-item label=" ">
            <el-input type="password" v-model="passwdCk"></el-input>
            <el-button size="small" @click="passwdCheck"></el-button>
            <span>{{ passwdCkmsg }}</span>
        </el-form-item>

        <el-form-item label="">
            <el-input type="text" v-model="MemberDTO.nm"></el-input>
        </el-form-item>

        <el-form-item label="">
            <el-col :span="12" style="display:flex;">
                <el-input type="text" v-model="MemberDTO.post" placeholder="" style="margin-right: 20px;"></el-input>
            </el-col>
            <el-button size="large" @click="execDaumPostcode"> </el-button>
            <el-input type="text" v-model="MemberDTO.addr1" placeholder=""></el-input>
            <el-input type="text" v-model="MemberDTO.addr2" placeholder=""></el-input>
        </el-form-item>

        <el-form-item label=" ">
            <el-input type="text" v-model="phoneNum" maxlength="11" placeholder="-    "></el-input>
        </el-form-item>

        
        <el-form-item label=" ">
            <el-col :span="7"  style="margin-right:8px;">
                <el-input type="email" v-model="MemberDTO.email1"></el-input>
            </el-col>
            <el-col class="line" :span="1" style="margin-right:8px;">@</el-col>
            <el-col :span="7">
                <el-input type="email" v-model="MemberDTO.email2"></el-input>
            </el-col>
            <el-col class="line" :span="1">&nbsp;</el-col>
            <el-col :span="7">
                <el-select v-model="MemberDTO.email2" placeholder="">
                    <el-option
                    v-for="item in emaildomain"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
            </el-col>
        </el-form-item>

    </el-form>
    <el-button type="warning" @click="signUp()"></el-button>
</el-col>
    
  </div>
</template>

<script 
  src="https://ajax.googleapis.com/ajax/libs/jquery/3.5.1/jquery.min.js"></script>
<script>
import axios from 'axios';

export default {
    name:'SignUp',
    data:function(){
        return{
            idCkmsg:"",
            passwdCk:"",
            passwdCkmsg:"",
            phoneNum:"",
            emaildomain: [
                {
                value: 'naver.com',
                label: 'naver.com'
                }, {
                value: 'gmail.com',
                label: 'gmail.com'
                }, {
                value: 'daum.net',
                label: 'daum.net'
                }, {
                value: '',
                label: ''
                }
            ],
            
            MemberDTO:{
                mbId:"",
                pw:"",
                nm:"",
                post:"",
                addr1:"",
                addr2:"",
                tel1:"010",
                tel2:"",
                tel3:"",
                email1:"",
                email2:""
            }
        }
    },//end data
    methods:{
        idCk:function(){
            console.log(this.MemberDTO.mbId);
            var idCkmsg = this.idCkmsg;
            var url=`http://localhost:8093/app/member/idCheck/${this.MemberDTO.mbId}`;
            axios.post(url,
            {
                mbId:this.MemberDTO.mbId
            }
            ).then(
                (res)=>{
                    console.log(res.data.message, this.MemberDTO.mdId);
                    if(res.data.message===" "){
                        this.idCkmsg="  .";
                    }else if(res.data.message===' '){
                        this.idCkmsg="  .";
                        this.MemberDTO.mbId='';
                    }
                }
            ).catch(
                (error)=>{
                    console.log(error);
                }
            )
        },
        passwdCheck: function(){
            if(this.MemberDTO.pw!=this.passwdCk){
                this.passwdCkmsg = '  .';
            }else{
                this.passwdCkmsg=" .";
            }
        },
        phoneSelect:function (event) {
          this.MemberDTO.tel1=event.target.value;
        },
        signUp:function (event) {
            //var memberDTO=this.MemberDTO;
            var pNm = this.phoneNum;
            this.MemberDTO.tel1 = pNm.substr(0,3);
            this.MemberDTO.tel2 = pNm.substr(3,4);
            this.MemberDTO.tel3 = pNm.substr(7);
            
            console.log("========", this.MemberDTO);
            
            if(this.MemberDTO.mbId==""){
                alert(" .");
            }else if(this.idCkmsg=="  ."||this.idCkmsg==''){
                alert(" .")
            }else if(this.MemberDTO.pw==""){
                alert(" .");
            }else if(this.passwdCk=="" || this.passwdCkmsg!=" ."){
                alert("  .");
            }else if(this.MemberDTO.nm==""){
                alert(" .");
            }else if(this.MemberDTO.post==""||this.MemberDTO.addr1==""||this.MemberDTO.addr2==""){
                alert(" .")
            }else if(this.MemberDTO.tel1==""||this.MemberDTO.tel2==""||this.MemberDTO.tel3==""){
                alert("  .")
            }else if(this.MemberDTO.email1==""||this.MemberDTO.email2==""){
                alert(" .")
            }
            var url="http://localhost:8093/app/member/memberAdd";
            axios.post(url,
            {
                mbId:this.MemberDTO.mbId,
                pw:this.MemberDTO.pw,
                nm:this.MemberDTO.nm,
                post:this.MemberDTO.post,
                addr1:this.MemberDTO.addr1,
                addr2:this.MemberDTO.addr2,
                tel1:this.MemberDTO.tel1,
                tel2:this.MemberDTO.tel2,
                tel3:this.MemberDTO.tel3,
                email1:this.MemberDTO.email1,
                email2:this.MemberDTO.email2
            }).then(
                (res)=>{
                    console.log(" ");
                    //   main 
                }
            ).catch(
                (error)=>{
                    console.log(error);
                }
            )
        },
        execDaumPostcode(){ //@click      .
            new window.daum.Postcode({
            oncomplete: (data) => { //function  =>  .
                //        .

                //       .
                //      ('') ,    .
                var roadAddr = data.roadAddress; //   
                var extraRoadAddr = ''; //   

                //    . ( )
                //     "//" .
                if(data.bname !== '' && /[||]$/g.test(data.bname)){
                    extraRoadAddr += data.bname;
                }
                //  ,   .
                if(data.buildingName !== '' && data.apartment === 'Y'){
                   extraRoadAddr += (extraRoadAddr !== '' ? ', ' + data.buildingName : data.buildingName);
                }
                //    ,     .
                if(extraRoadAddr !== ''){
                    extraRoadAddr = ' (' + extraRoadAddr + ')';
                }

                //      .
                this.MemberDTO.post = data.zonecode;
                this.MemberDTO.addr1 = roadAddr;
            }
            }).open();
        }//end          
    }//end methods

}
</script>

<style>
.el-button{
    margin-right: 10px;
}
</style>